.main-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 75vh;
    margin: 100px;
  }
  
  .triangle-objects {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
  }
  
  .container-ordi {
    position: absolute;
    top: 5vh;
    left: 75vh;
  }
  
  .container-livre {
    position: absolute;
    top: 70vh;
    left: 30vh;
  }
  
  .container-casque {
    position: absolute;
    top: 30vh;
    left: 5vh;
  }

  .text-main-page .typewriter-text .text{
    font-size: 75px;
  }
  
  .text-main-page {
    text-align: right;
    font-size: 24px;
    margin-right: 50px;
    max-width: 800px; /* Pour éviter un texte trop étalé sur grand écran */
    margin-left: auto; /* Pour centrer la boîte avec alignement à droite */
  }
  
  .text-main-page p {
    text-align: justify; /* Justifier le texte */
    font-size: 20px; /* Légèrement plus petit pour une meilleure lisibilité */
    line-height: 1.5; /* Espacement amélioré pour le confort de lecture */
  }
  
  .redirect-btn {
    text-align: right; /* Aligner les boutons à droite */
  }
  
  .redirect-btn .ant-btn {
    margin-top: 40px;
    margin-left: 10px;
  }

  blockquote {
    font-size: 20px;
    font-style: italic;
    color: #555; /* Couleur douce pour le texte */
    border-left: 5px solid #1890ff; /* Barre colorée à gauche */
    padding-left: 20px; /* Espacement pour le texte */
    margin: 20px 0;
    line-height: 1.6;
  }

  @media screen and (max-width: 1250px) {
    .container-ordi {
        position: absolute;
        top: 5px;
        left: 5px;
      }
      
      .container-livre {
        position: absolute;
        top: 75vh;
        left: 15vh;
      }
      
      .container-casque {
        position: absolute;
        display: none;
      }
  }

  @media screen and (max-width:700px) {
    .container-ordi {
        position: absolute;
        display: none;
      }
      
      .container-livre {
        position: absolute;
        display: none;
      }
      
      .container-casque {
        position: absolute;
        display: none;
      }
  }