.PROJECT.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  width: 200px;
  height: max-content;
  align-items: center;
  text-align: center;
  background-color: white;
}

.PROJECT.card:hover {
  transform: scale(1.05);
}

.PROJECT.image-container {
  display: block;
  align-items: center;
  justify-content: center;
}

.PROJECT.loader {
  position: absolute;
}

.PROJECT.image {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

.PROJECT.title {
  font-size: 20px;
  margin: 10px 0;
}

.PROJECT.text {
  color: #272727;
  font-size: 14px;
}

.PROJECT.tags {
  margin-top: 10px;
}
