.project-table {
  padding: 20px;
  width: 200;
  height: 300;

  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  background-color: white;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: 100px;
  margin-right: 100px;
}

.project-table .ant-col {
  justify-content: center;
}

.project-table .ant-row {
  width: 100%;
  flex-wrap: wrap;
}

.project-table .project-col {
  justify-content: center;
}

.project-tags {
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 8px;
}

.filters {
  gap: 16px;
  margin-bottom: 20px;
}

